.footer {
    background-color: #223f65;
    color: white;
    padding: 40px 20px;
  }
  
  .footer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .footer-section {
    flex: 1 1 200px;
    margin: 10px;
  }
  
  .footer-section h3, .footer-section h4 {
    color: #7182a2;
    margin-bottom: 15px;
  }
  
  .footer-section ul {
    list-style-type: none;
    padding: 0;
  }
  
  .footer-section ul li {
    margin: 5px 0;
  }
  
  .footer-section ul li a {
    color: white;
    text-decoration: none;
  }
  
  .footer-section ul li a:hover {
    text-decoration: underline;
  }
  
  .footer-section a {
    color: white;
    text-decoration: none;
  }
  
  .footer-bottom {
    text-align: center;
    margin-top: 20px;
  }
  
  .social-icons a {
    color: white;
    margin: 0 10px;
    font-size: 1.2em;
    text-decoration: none;
  }
  
  .social-icons a:hover {
    color: #006aff;
  }
  
/* Mobile (up to 600px) */
@media only screen and (max-width: 600px) {
    .footer {
        padding: 15px; /* Adjust padding for smaller screens */
    }

    .footer p {
        font-size: 0.9rem; /* Slightly smaller font size for mobile */
    }
}

/* iPad / Tablet (601px to 768px) */
@media only screen and (min-width: 601px) and (max-width: 768px) {
    .footer {
        padding: 18px; /* Adjust padding for tablets */
    }

    .footer p {
        font-size: 1rem; /* Default font size for tablets */
    }
}

/* Laptop (769px to 1024px) */
@media only screen and (min-width: 769px) and (max-width: 1024px) {
    .footer {
        padding: 20px; /* Default padding for laptops */
    }

    .footer p {
        font-size: 1rem; /* Default font size for laptops */
    }
}

/* Desktop (1025px and above) */
@media only screen and (min-width: 1025px) {
    .footer {
        padding: 25px; /* Larger padding for desktop */
    }

    .footer p {
        font-size: 1.1rem; /* Slightly larger font for desktop */
    }
}
